/* src/components/TeamMatches/index.css */
.team-matches-container {
  min-height: 100vh;
  display: flex;
  justify-content: center;
  padding: 20px;
}
.team-matches-content-container {
  width: 100%;
  max-width: 1100px;
}
.team-banner {
  width: 100%;
  border-radius: 12px;
}
.recent-matches-list {
  list-style-type: none;
  padding-left: 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

/* Dynamic Backgrounds */
.RCB { background-image: linear-gradient(to top, #1e293b, #a4261d); }
.KKR { background-image: linear-gradient(to top, #1e293b, #5755a7); }
.KXP { background-image: linear-gradient(to top, #1e293b, #d91c1f); }
.CSK { background-image: linear-gradient(to top, #1e293b, #f7db00); }
.RR { background-image: linear-gradient(to top, #1e293b, #da237b); }
.MI { background-image: linear-gradient(to top, #1e293b, #13418b); }
.SH { background-image: linear-gradient(to top, #1e293b, #f26d22); }
.DC { background-image: linear-gradient(to top, #1e293b, #4f5db0); }