/* src/components/MatchCard/index.css */
.match-card-item {
  width: 48%;
  background-color: #0f172a;
  border-radius: 12px;
  padding: 20px;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.match-card-logo {
  width: 80px;
  height: 80px;
}
.match-card-team-name {
  color: #ffffff;
  font-family: 'Bree Serif', serif;
  font-size: 20px;
  margin-top: 10px;
  margin-bottom: 10px;
}
.match-card-result {
  color: #ffffff;
  font-family: 'Bree Serif', serif;
  font-size: 14px;
  margin-bottom: 15px;
}
.match-status {
  font-family: 'Bree Serif', serif;
  font-size: 20px;
}
.match-won {
  color: #18ed66; /* Green */
}
.match-lost {
  color: #e31a1a; /* Red */
}
@media screen and (min-width: 768px) {
  .match-card-item {
    width: 30%;
  }
}