/* src/components/LatestMatch/index.css */
.latest-match-card {
  background-color: #0f172a;
  border-radius: 12px;
  padding: 24px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}
.latest-match-details-main {
  width: 100%;
}
.latest-match-team-name {
  color: #ffffff;
  font-family: 'Bree Serif', serif;
  font-size: 24px;
}
.latest-match-date,
.latest-match-venue,
.latest-match-result {
  color: #ffffff;
  font-family: 'Bree Serif', serif;
  font-size: 16px;
}
.latest-match-team-logo {
  width: 100px;
  margin: 20px 0;
}
.latest-match-details-info {
  width: 100%;
  text-align: left;
}
.latest-match-info-heading {
  color: #ffffff;
  font-family: 'Bree Serif', serif;
  font-size: 16px;
  font-weight: bold;
}
.latest-match-info-text {
  color: #ffffff;
  font-family: 'Bree Serif', serif;
  font-size: 14px;
}
/* Responsive styles for larger screens */
@media screen and (min-width: 768px) {
  .latest-match-details-main {
    width: 30%;
  }
  .latest-match-team-logo {
    width: 200px;
    order: 1; /* This moves the logo to the right side */
    margin: 0;
  }
  .latest-match-details-info {
    width: 45%;
    text-align: right;
  }
}