/* src/components/TeamCard/index.css */
.team-item {
  width: 45%;
  margin: 10px;
  background-color: #ffffff33;
  border: 1px solid #ffffff;
  border-radius: 12px;
}
.team-link {
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 10px 20px;
}
.team-logo {
  width: 60px;
  height: 60px;
}
.team-name {
  color: #ffffff;
  font-family: 'Bree Serif', serif;
  font-size: 16px;
  margin-left: 10px;
  text-align: center;
  flex-grow: 1;
}
